<script lang="coffeescript">var dispatch, filter, list, refreshList, resourceList, select, selectAndRefresh;

import {
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

import {
  IconSvg,
  IconToggleSvg
} from "braintapper-svelte-icon";

import {
  debug,
  MenuContainer,
  MenuDivider,
  MenuItem
} from "../../../Braintapper";

import {
  Lists
} from "../../../Application";

export var value = "";

export var style = "";

select = function(value) {
  return dispatch("tippy-event", {
    event: "select",
    value: value
  });
};

////             _   _     _     _   
////   __ _  ___| |_| |   (_)___| |_ 
////  / _` |/ _ \ __| |   | / __| __|
//// | (_| |  __/ |_| |___| \__ \ |_ 
////  \__, |\___|\__|_____|_|___/\__|
////  |___/                          
list = [];

resourceList = new Lists;

refreshList = function(items) {
  console.log(list);
  return list = items;
};

selectAndRefresh = function(view) {
  return resourceList.select({
    doctype: "List"
  }, refreshList);
};

selectAndRefresh();

filter = function(val) {
  var output;
  console.log(`FILTER: ${val}`);
  if (list != null) {
    list.sortBy("name");
    if (val.length === 0) {
      return list;
    } else {
      output = list.filter(function(o) {
        return o.name.toLowerCase().includes(val.toLowerCase());
      });
      return output;
    }
  } else {
    return [];
  }
};

$: filteredItems = filter(value, list);

$: latestStyle = style;
</script>


<div panel style={latestStyle}>
<MenuContainer>
  {#each filteredItems as item, index}
    <MenuItem on:click={select(item)}>
      <div class="name">{item.name}</div>
      <div class="id">{item._id}</div>
    </MenuItem>
    {#if index < (filteredItems.length - 1)}
      <MenuDivider />  
    {/if}
  {/each}



</MenuContainer>

</div>

<style lang="sass">[panel] {
  overflow-y: auto;
  max-height: 200px;
  border: 1px solid var(--black);
}

.id {
  font-size: var(--font-xs);
  color: var(--light-blue);
}</style>