<script lang="coffeescript">var checkDate, dispatch, ellipsisActive, toggleExpanded, updateDueDate, updateItem;

import {
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

import {
  FlexRow,
  FlexRowCell
} from "braintapper-svelte-flex";

import Indent from "braintapper-svelte-indent";

import {
  ButtonIcon
} from "braintapper-svelte-button";

import {
  IconSvg,
  IconToggleSvg
} from "braintapper-svelte-icon";

import {
  debug,
  FormatEllipsis,
  InlineInput,
  SearchHighlighter
} from "../../../Braintapper";

import {
  ProgressPopover,
  DueDatePopover
} from "../../../Application";

export var item = void 0;

export var context = "list";

export var classes = void 0;

export var keywords = "";

toggleExpanded = function() {
  console.log(`toggle Expanded ${context}`);
  item.is_expanded[context] = item.toggle_expanded(context);
  item = item; // repaint this item
  return dispatch("update", {
    _id: item._id,
    is_expanded: item.is_expanded
  });
};

updateItem = function(e) {
  return dispatch("update", e.detail);
};

updateDueDate = function(e) {
  console.log(`updateDueDate : ${JSON.stringify(e.detail)}`);
  item.due_date = Date.create(e.detail).format("{yyyy}-{MM}-{dd}");
  item = item; // force refresh
  console.log("ITEMROW predispatch");
  return updateItem({
    detail: {
      _id: item._id,
      due_date: e.detail
    }
  });
};

ellipsisActive = false;

checkDate = function(val) {
  if (val != null) {
    if (val.due_date != null) {
      return Date.create(val.due_date);
    } else {
      return null;
    }
  } else {
    return null;
  }
};

$: dueDate = checkDate(item);

$: showTooltip = ellipsisActive;
</script>


<FlexRow {classes}>
    <FlexRowCell flex="initial" style="width: {(item.ancestry || []).length * 32}px">
      <Indent level={(item.ancestry || []).length} width={32}/>
    </FlexRowCell>
    {#if context != "search"}
      <FlexRowCell classes="collapsible">
        <div collapsible>
          {#if item.is_parent}
            <ButtonIcon on:click={toggleExpanded()}>
              <IconToggleSvg value={item.is_expanded[context]} whenTrue="expanded" whenFalse="collapsed"/>
            </ButtonIcon>
            
          {:else}
            <IconSvg/>
          {/if}
        </div>
      </FlexRowCell>  
    {/if}  
    <FlexRowCell flex>
      <FlexRow>
        <FlexRowCell flex="initial">
          <div progress>
            <ProgressPopover item={item} value={item.progress} on:update={updateItem} on:open on:close/>
          </div>
        </FlexRowCell>
        <FlexRowCell flex style="width: 100px">
          
            {#if context == "list"}

              <InlineInput {item} on:update={updateItem}>
                <!-- dry this up -->
                {#if item.is_hidden}
                  <FormatEllipsis bind:ellipsisActive={ellipsisActive}>Text for this is hidden. You can edit it to see it, or unhide it using the menu.</FormatEllipsis>
                {:else}
                  <FormatEllipsis bind:ellipsisActive={ellipsisActive}>
                    {#if item.is_ongoing}
                      <span><IconSvg icon="ongoing" size="12"/></span>
                    {/if}                  
                    <SearchHighlighter text={item.name || "No Name"} keywords={keywords.split(" ")}/>
                  </FormatEllipsis>

                {/if}
              </InlineInput>

            {:else}

              {#if item.is_hidden}
                <FormatEllipsis bind:ellipsisActive={ellipsisActive}>Text for this is hidden. You can edit it to see it, or unhide it using the menu.</FormatEllipsis>
              {:else}
                <FormatEllipsis bind:ellipsisActive={ellipsisActive}>
                  {#if item.is_ongoing}
                    <span><IconSvg icon="ongoing" size="12"/></span>
                    &nbsp;
                  {/if}                
                  <SearchHighlighter text={item.name || "No Name"} keywords={keywords.split(" ")}/>
                </FormatEllipsis>
              {/if}            
            {/if}          
          
        </FlexRowCell>
        <FlexRowCell flex="initial">
          <div visible-on-hover>
            <DueDatePopover value={item.due_date} on:update={updateDueDate} on:open on:close/>
          </div>
        </FlexRowCell>
      
      </FlexRow>
    </FlexRowCell>
</FlexRow>

<style lang="sass">[progress] {
  padding-right: 8px;
}

[collapsible] {
  line-height: 24px;
}
[collapsible] svg, [collapsible] svg.menu {
  display: inline;
}</style>