forceSync = function() {
  console.log(remoteDb);
  if (typeof remoteDb !== "undefined" && remoteDb !== null) {
    console.log("syncing to remote db");
    return db.sync(remoteDb, {
      retry: true
    }).on('complete', function() {
      return console.log("complete!");
    }).on('change', function(change) {
      // yo, something changed!
      console.log("sync change");
      return console.log(change);
    }).on('paused', function(info) {
      // replication was paused, usually because of a lost connection
      console.log("sync paused");
      return console.log(info);
    }).on('active', function(info) {
      // replication was resumed
      console.log("sync active");
      return console.log(info);
    }).on('error', function(err) {
      console.log("sync error");
      return console.log(err);
    });
  }
};

notificationPermssionRequest = function() {
  return Notification.requestPermission().then(function(permission) {
    var notification;
    // If the user accepts, let''s create a notification
    if (permission === 'granted') {
      notification = new Notification('Hi there!');
    } else {
      console.log("not granted");
    }
  });
};

if (electron) {
  edition = "Desktop";
} else {
  edition = "Web";
}

$: disableReplication = (replicationEnabled != 'true');

// resource
cleanLists = function() {
  return db.find({
    selector: {
      doctype: "List"
    }
  }).then(function(result) {
    return db.bulkDocs(result.docs.map(function(doc) {
      doc["_deleted"] = true;
      return doc;
    })).then(function(result) {
      return console.error("Lists deleted");
    });
  });
};

cleanListItems = function() {
  return db.find({
    selector: {
      doctype: "ListItem"
    }
  }).then(function(result) {
    return db.bulkDocs(result.docs.map(function(doc) {
      doc["_deleted"] = true;
      return doc;
    })).then(function(result) {
      return console.error("cleanListItems deleted");
    });
  });
};

compact = function() {
  return db.compact().then(function(result) {
    console.log("COMPACTED");
    return console.log(result);
  });
};

migrate = function() {
  var migrateScb, output, staging;
  console.error("start migration");
  staging = [];
  output = [];
  migrateScb = function(docs) {
    var blob, jsonString;
    docs.forEach(function(doc) {
      return console.log(`doc: ${doc.name}, list_items: ${doc.list_items.length}`);
    });
    
    // doc.migrate(staging)
    console.table(staging);
    staging.forEach(function(item) {
      var newModel;
      if (item.type === "List") {
        newModel = new List(item);
      } else {
        newModel = new ListItem(item);
      }
      return output.append(newModel.pouchify());
    });
    jsonString = JSON.stringify(output, null, 2);
    blob = new Blob([jsonString], {
      type: "text/plain;charset=utf-8"
    });
    // note - the md5 works on the string loaded in the file, which is not the same as the file''s md5
    // use a different handler for electron
    FileSaver.saveAs(blob, `braintapper-radar-migrated-${Date.create().toTimestampId()}--${md5(jsonString)}.json`);
    return db.bulkDocs(output).then(function(result) {
      console.error("imported new lists and list items");
      return console.debug(result);
    });
  };
  return resource.selectAll(migrateScb);
};

