importClipboard = function(e) {
  var maxIndex, newListItems, parsedLines, rootItems;
  console.error("importClipboard");
  rootItems = list.working_tree.filter(function(o) {
    return o.ancestry.length === 0;
  });
  if (rootItems.length > 0) {
    maxIndex = rootItems.max(function(o) {
      return o.index;
    });
    maxIndex = maxIndex.index;
  } else {
    maxIndex = 0;
  }
  console.error(`maxindex: ${maxIndex}`);
  parsedLines = e.detail.list_items.lines().filter(function(o) {
    return o.length > 0;
  });
  console.log(parsedLines);
  newListItems = parsedLines.map(function(line, index) {
    var modelized;
    modelized = new ListItem({
      list_id: e.detail._id,
      name: line,
      ancestry: [],
      index: maxIndex + ((index + 1) * 1000)
    });
    return modelized.pouchify();
  });
  console.log(newListItems);
  return db.bulkDocs(newListItems).then(function(result) {
    console.error(`importClipboard - inserted ${newListItems.length}`);
    console.log(result);
    return getList();
  });
};

////  _                            _    ____ _ _       _                         _ _____     _     _     _   ___ _                 
//// (_)_ __ ___  _ __   ___  _ __| |_ / ___| (_)_ __ | |__   ___   __ _ _ __ __| |_   _|__ | |   (_)___| |_|_ _| |_ ___ _ __ ___  
//// | | '_ ` _ \| '_ \ / _ \| '__| __| |   | | | '_ \| '_ \ / _ \ / _` | '__/ _` | | |/ _ \| |   | / __| __|| || __/ _ \ '_ ` _ \ 
//// | | | | | | | |_) | (_) | |  | |_| |___| | | |_) | |_) | (_) | (_| | | | (_| | | | (_) | |___| \__ \ |_ | || ||  __/ | | | | |
//// |_|_| |_| |_| .__/ \___/|_|   \__|\____|_|_| .__/|_.__/ \___/ \__,_|_|  \__,_| |_|\___/|_____|_|___/\__|___|\__\___|_| |_| |_|
////             |_|                            |_|                                                                                
importClipboardToListItem = function(e) {
  var baseIndex, buffer, bufferRange, indexGap, indexIncrement, indexOverride, newListItems, nextIndex, nextItem, parsedLines, previousItem, siblingItems, startIndex, thisIndex;
  console.error("importClipboardToListItem");
  indexOverride = Object.has(e.detail, "index"); // then index override
  parsedLines = e.detail.list_items.lines().filter(function(o) {
    return o.length > 0;
  });
  console.log(parsedLines);
  siblingItems = list.working_tree.filter(function(o) {
    return o.ancestry.isEqual(e.detail.ancestry);
  });
  siblingItems.sortBy("index");
  indexIncrement = 1000;
  if (siblingItems.length > 0) {
    // need to check before and after
    if (indexOverride) {
      console.error("INDEX OVERRIDE");
      thisIndex = siblingItems.findIndex({
        _id: e.detail._id
      });
      console.error(`ThisINdex ${thisIndex}`);
      console.log(siblingItems[thisIndex]);
      if (thisIndex >= 0) {
        switch (thisIndex) {
          case 0: // first or (first and last)
            baseIndex = e.detail.index - (parsedLines * 1000);
            break;
          case siblingItems.length - 1: // last
            baseIndex = e.detail.index; // "somewhere in between"
            break;
          default:
            console.error(`INDEX OVERRIDE BETWEEN 2 ITEMS [${thisIndex}]`);
            previousItem = siblingItems[thisIndex];
            nextItem = siblingItems[thisIndex + 1];
            console.log(siblingItems);
            console.debug(previousItem);
            console.debug(nextItem);
            startIndex = e.detail.index;
            console.error(`startIndex ${startIndex}`);
            nextIndex = nextItem.index;
            console.error(`nextIndex ${nextIndex}`);
            if (nextIndex === startIndex) { // yikes
              console.error("YIKES CASE!");
              // fuck it, make it all the same index
              baseIndex = nextIndex;
              indexIncrement = 0;
            } else {
              indexGap = nextIndex - startIndex;
              console.warn(`indexGap ${indexGap}`);
              buffer = indexGap * 0.1; // 10 percent
              bufferRange = (nextIndex - buffer) - startIndex;
              console.warn(`BUFFER RANGE: ${nextIndex - buffer} - ${startIndex} = ${bufferRange}`);
              indexIncrement = bufferRange / parsedLines.length;
              baseIndex = startIndex;
              console.warn(`indexIncrement: ${indexIncrement}`);
              console.warn(`baseIndex: ${baseIndex}`);
            }
        }
      }
      baseIndex = e.detail.index;
    } else {
      console.error("NO INDEX OVERRIDE");
      baseIndex = siblingItems.max(function(o) {
        return o.index;
      });
      baseIndex = baseIndex.index;
    }
  } else {
    baseIndex = 0;
  }
  console.error(`baseIndex: ${baseIndex}`);
  newListItems = parsedLines.map(function(line, index) {
    var modelized;
    modelized = new ListItem({
      list_id: list._id,
      name: line,
      ancestry: e.detail.ancestry,
      index: baseIndex + ((index + 1) * indexIncrement)
    });
    return modelized.pouchify();
  });
  console.log(newListItems);
  return db.bulkDocs(newListItems).then(function(result) {
    console.error(`importClipboard - inserted ${newListItems.length}`);
    console.log(result);
    return getList();
  });
};


