<script lang="coffeescript">var forceSync, mounted, syncSettings, toggleSync, updateSetting;

import {
  onMount
} from "svelte";

import {
  PouchSyncSettings,
  PouchIsSyncing,
  PageContent,
  PageHeader
} from "../../Application";

import Button from "braintapper-svelte-button";

import {
  debug,
  InlineInput,
  InlinePassword,
  Checkbox
} from "../../Braintapper";

syncSettings = void 0;

toggleSync = function() {
  console.log(`togglesync - last state: ${syncSettings.enabled}`);
  syncSettings.enabled = !syncSettings.enabled;
  // localStorage.setItem "radar_sync", replicationEnabled
  //console.log "new settings:"
  //console.debug syncSettings
  return PouchSyncSettings.set(syncSettings);
};

// web = !window.electron
updateSetting = function(e) {
  var value;
  delete e.detail._id; // is extraneous
  value = e.detail;
  Object.keys(value).forEach(function(key) {
    if (key === "auto_sync_interval_seconds") {
      return syncSettings[key] = value[key] * 1000;
    } else {
      return syncSettings[key] = value[key];
    }
  });
  return PouchSyncSettings.set(syncSettings);
};

forceSync = function() {
  console.log("force sync");
  window.pouch.sync_pending = true;
  return window.pouchSync();
};

/* for Electron
notificationPermssionRequest = ()->
Notification.requestPermission().then (permission) ->
 * If the user accepts, let''s create a notification
  if permission == 'granted'
    notification = new Notification('Hi there!')
  else
    console.log "not granted"
  return
 */
////              __  __                   _   
////   ___  _ __ |  \/  | ___  _   _ _ __ | |_ 
////  / _ \| '_ \| |\/| |/ _ \| | | | '_ \| __|
//// | (_) | | | | |  | | (_) | |_| | | | | |_ 
////  \___/|_| |_|_|  |_|\___/ \__,_|_| |_|\__|
mounted = false;

onMount(function() {
  mounted = true;
  return PouchSyncSettings.subscribe(function(val) {
    return syncSettings = val;
  });
});
</script>
<style lang="sass">article {
  margin: 0px;
}

button [disabled] {
  background: var(--lightest-gray) !important;
}</style>

<svelte:head>
  <title>Sync Settings</title>
</svelte:head>
<PageContent>
  <PageHeader><h1 title>Sync Settings</h1></PageHeader>
  <article type="content">
   
    {#if mounted}
      <div>
          <Button state="primary" on:click={forceSync} disabled={!syncSettings.enabled}>Force Sync</Button>
      </div>



      
      <h3>Synchronization</h3>
      <div>
        This application can sync with any CouchDB compatible database (non-partitioned).
      </div>
      <div>
        <field>
          <Checkbox checked={syncSettings.enabled} key="sync" label="Enable Synchronization with Remote Database" on:change={toggleSync}/>
          <aside>The database will be synchronized every time an item has been added, edited or deleted.</aside>
        </field>
        <field>
          <Checkbox checked={syncSettings.auto_sync} key="sync" label="Enable Automatic Synchronization" on:change={toggleSync}/>
          <aside>Automatic synchronization means a synchronization will happen every # seconds, irrespective of whether an item has been added, edited or deleted.</aside>
        </field>        
        <field>
          <label for="field">Automatic Sync Interval in seconds</label>
          <InlineInput item={syncSettings} inputAttribute="auto_sync_interval" on:update={updateSetting} label="Database URI is hidden. Double click to edit." placeholder="DB URI here">
            {#if syncSettings.auto_sync_interval}
              <span>{syncSettings.auto_sync_interval }</span>
            {:else}
              <span>Remote database not defined. Double click to edit.</span>
            {/if}
          </InlineInput>
        </field>        
      </div>
      <h2>Remote Database</h2>

      <h3>Database URI</h3>
      <h4>
      https://{syncSettings.remote_database_user}:[password not shown]@{syncSettings.remote_database_host}:{syncSettings.remote_database_port}/{syncSettings.remote_database_name}
      </h4>
      <div>
        <field>
          <label for="field">Database Host:</label>
          <InlineInput item={syncSettings} inputAttribute="remote_database_host" on:update={updateSetting} placeholder="DB URI here">
            {#if (syncSettings.remote_database_host || "").length > 0}
              <span>{syncSettings.remote_database_host}</span>
            {:else}
              <span>Remote database not defined. Double click to edit.</span>
            {/if}
          </InlineInput>
        </field>
        <field>
          <label for="field">Database Port (HTTPS):</label>
          <InlineInput item={syncSettings} inputAttribute="remote_database_port" on:update={updateSetting} placeholder="DB URI here">
            {#if syncSettings.remote_database_port}
              <span>{syncSettings.remote_database_port}</span>
            {:else}
              <span>Remote database port not defined. Double click to edit. Default for HTTPS is 6984.</span>
            {/if}
          </InlineInput>
        </field>

        <field>
          <label for="field">Database Name:</label>
          <InlineInput item={syncSettings} inputAttribute="remote_database_name" on:update={updateSetting} placeholder="DB URI here">
            {#if (syncSettings.remote_database_name || "").length > 0}
              <span>{syncSettings.remote_database_name}</span>
            {:else}
              <span>Remote database not defined. Double click to edit.</span>
            {/if}
          </InlineInput>
        </field>

        <field>
          <label for="field">Database User:</label>
          <InlineInput item={syncSettings} inputAttribute="remote_database_user" on:update={updateSetting} placeholder="DB URI here">
            {#if (syncSettings.remote_database_user || "").length > 0}
              <span>{syncSettings.remote_database_user}</span>
            {:else}
              <span>Remote database not defined. Double click to edit.</span>
            {/if}
          </InlineInput>
        </field>

        <field>
          <label for="field">Database Password:</label>
          <InlineInput item={syncSettings} inputAttribute="remote_database_password" on:update={updateSetting} placeholder="DB URI here">
            {#if (syncSettings.remote_database_password || "").length > 0}
              <span>Password is defined but not shown</span>
            {:else}
              <span>Remote database not defined. Double click to edit.</span>
            {/if}
          </InlineInput>
        </field>




        <field>
          <button type="button" primary>Test Database Connection</button>
        </field>
        <div>
          <h4>Warnings</h4>
          <ul>

            <li>
              URL must be https, and server must have valid certificate.
            </li>
          </ul>
        </div>
      </div>
    {/if}


  </article>

</PageContent>
