<script lang="coffeescript">// todo use a format string instead of css
var amountClasses, containerClasses, formatAmount;

export var value = 0;

export var showCurrency = true;

export var showParentheses = true;

export var showCurrencyBefore = true;

containerClasses = [];

amountClasses = [];

formatAmount = function(amount) {
  // reset the containerClasses
  containerClasses = [];
  amountClasses = [];
  if (amount !== 0) {
    amount = amount / 100;
  }
  if (showCurrency) {
    amountClasses.append("before-dollar");
  }
  if (amount < 0) {
    containerClasses.append("negative");
    if (showParentheses) {
      containerClasses.append("parentheses");
    }
  }
  return Math.abs(amount).format(2);
};

$: ledgerAmount = formatAmount(value);
</script>
<span class={containerClasses.join(" ")}><span class={amountClasses.join(" ")}>{ledgerAmount}</span></span>

<style lang="sass">.negative {
  color: var(--red);
}

.parentheses:before {
  content: "(";
}
.parentheses:after {
  content: ")";
}

.before-dollar:before {
  content: "$";
}

.after-dollar:after {
  content: "$";
}</style>