<script lang="coffeescript">var checkContent, content;

import marked from "marked";

content = void 0;

window._content = content;

checkContent = function(item) {
  if (item != null) {
    return item.innerHTML;
  } else {
    return "";
  }
};

$: markup = checkContent(content);
</script>
<div>
  <div markup bind:this={content}>
    <slot/>
  </div>
  <div>{@html marked(markup)}</div>
</div>

<style lang="sass">[markup] {
  display: none;
}</style>