<script lang="coffeescript">// todo: implement cancel
var click, clickCount, clickReset, delayedReset, dispatch, double, handleBlur, handleInput, handleKeyup, inputEl, label, originalValue, single, value;

import {
  tick,
  createEventDispatcher
} from 'svelte';

dispatch = createEventDispatcher();

// todo: cancel

// Props
// export value = ''
export var style = void 0;

export var inputStyle = void 0;

export var type = 'text';

export var placeholder = '';

export var labelClasses = '';

export var inputClasses = '';

export var item = {
  id: "",
  name: ""
};

export var inputAttribute = "name";

export var idAttribute = "_id";

export var doubleClick = true;

export var id = void 0;

export var delay = 250;

export var editing = false;

// todo: export selectAllOnEdit = false
// todo: export forceEditMode = false
inputEl = void 0;

label = void 0;

originalValue = void 0;

value = void 0;

// Computed
// todo: clean these up
$: isText = type === 'text';

$: isNumber = type === 'number';

$: if (isNumber) {
      label = value === '' ? placeholder : value;
    } else if (isText) {
      label = value ? value : placeholder;
    }
const toggle = async (_) => {
  editing = !editing;

  originalValue = value;
  if (editing) {
    await tick();
    inputEl.focus();
    inputEl.select();
    value = "" + item[inputAttribute];

  }
};

handleInput = function(e) {
  if (isNumber) {
    value = +e.target.value;
  } else {

  }
  return value = e.target.value;
};

handleKeyup = function(e) {
  var eventObject, unchanged;
  dispatch("keyup", value);
  switch (e.keyCode) {
    case 13:
      inputEl.blur();
      unchanged = value === item[idAttribute];
      if (!(unchanged && (value.length > 0))) {
        console.log(`value: ${value}`);
        eventObject = {
          _id: item[idAttribute]
        };
        eventObject[inputAttribute] = value;
        console.log("eventObject");
        console.log(eventObject);
        return dispatch("update", eventObject);
      }
      break;
    case 27:
      value = originalValue;
      return inputEl.blur();
  }
};

// }
handleBlur = function(_) {
  return toggle();
};

// Click tracking
clickCount = 0;

clickReset = function() {
  switch (clickCount) {
    case 1:
      return single();
    case 2:
      return double();
    default:
      return clickCount = 0;
  }
};

// }
delayedReset = clickReset.debounce(delay);

single = function() {
  clickCount = 0;
  if (!doubleClick) {
    toggle();
  }
  return dispatch("clicked", id);
};

double = function() {
  clickCount = 0;
  if (doubleClick) {
    toggle();
  }
  return dispatch("doubleclicked", id);
};

click = function() {
  clickCount++;
  return delayedReset();
};
</script>

{#if editing && (isText || isNumber)}
  <input
    class={inputClasses}
    style={inputStyle}
    bind:this={inputEl}
    {type}
    {value}
    {placeholder}
    on:click|stopPropagation
    on:input={handleInput}
    on:keyup={handleKeyup}
    on:blur={handleBlur}>
{:else}
  <div
    {id}
    class={labelClasses}
    {style}>
    <span on:click={click}><slot><span>{item[inputAttribute]}<span spacer/></span></slot></span>
  </div>
{/if}
<style lang="sass">input, div, div span {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  border: 0px solid transparent;
  padding: 0px;
}

input {
  width: 100%;
  color: var(--dark-red);
}

input::selection {
  background: var(--dark-red);
  color: white;
}

div {
  min-height: var(--font-xl);
  border: 1px solid transparent;
}</style>