<script lang="coffee">var changePreset, containerWidth, contentElement, dispatch, el, horizonLabel, iconClass, iconPosition, jumpToPreset, moveThumbIcon, parentResize, presets, setIconClass, sliderValue, snapToPreset, translatedValue, windowHeight, windowResize, windowScrollY, windowWidth;

import {
  onMount,
  onDestroy,
  createEventDispatcher
} from "svelte";

export var value = void 0;

import {
  leftSplitContentElement
} from "../../../Application";

sliderValue = 0;

dispatch = new createEventDispatcher;

el = {
  width: 0
};

windowWidth = void 0;

windowHeight = void 0;

containerWidth = 0;

iconPosition = 0;

iconClass = "center";

horizonLabel = "";

iconPosition = "left: 0px";

// preset horizon values
presets = [
  {
    rangeLabel: "Today",
    label: "0d",
    labelPosition: 1,
    tickPosition: 0,
    iconClass: "center",
    tickRange: Number.range(0,
  5)
  },
  {
    rangeLabel: "Yesterday, Today and Tomorrow",
    label: "1d",
    labelPosition: 10,
    tickPosition: 10,
    iconClass: "center",
    tickRange: Number.range(6,
  15)
  },
  {
    rangeLabel: "7 days",
    label: "7d",
    labelPosition: 20,
    tickPosition: 20,
    iconClass: "center",
    tickRange: Number.range(16,
  25)
  },
  {
    rangeLabel: "14 days",
    label: "14d",
    labelPosition: 30,
    tickPosition: 30,
    iconClass: "inner",
    tickRange: Number.range(26,
  37)
  },
  {
    rangeLabel: "1 Month",
    label: "1m",
    labelPosition: 45,
    tickPosition: 45,
    iconClass: "inner",
    tickRange: Number.range(38,
  52)
  },
  {
    rangeLabel: "3 Months",
    label: "3m",
    labelPosition: 60,
    tickPosition: 60,
    iconClass: "inner",
    tickRange: Number.range(53,
  67)
  },
  {
    rangeLabel: "6 Months",
    label: "6m",
    labelPosition: 75,
    tickPosition: 75,
    iconClass: "outer",
    tickRange: Number.range(68,
  82)
  },
  {
    rangeLabel: "12 Months",
    label: "12m",
    labelPosition: 90,
    tickPosition: 90,
    iconClass: "outer",
    tickRange: Number.range(83,
  95)
  },
  {
    rangeLabel: "All Time",
    label: "&infin;",
    labelPosition: 101,
    tickPosition: 100,
    iconClass: "outer",
    tickRange: Number.range(96,
  100)
  }
];

setIconClass = function() {
  var found;
  found = presets.find(function(notch) {
    return notch.tickRange.contains(sliderValue);
  });
  return iconClass = found.iconClass;
};

moveThumbIcon = function(width) {
  var iconStart;
  // move the thumb image with the actual range slider's thumb
  iconStart = containerWidth * (sliderValue / 100); // subtract midpoint
  // Offsets
  // 0: 0
  // 50: -13
  // 100: -26  100/26= 3.846153846
  iconPosition = `left: ${iconStart - (sliderValue / 3.846153846)}px`;
  return setIconClass();
};

windowResize = function(width, height, node) {
  containerWidth = node.clientWidth;
  return moveThumbIcon(width);
};

translatedValue = function(val) {
  var found;
  found = presets.find(function(notch) {
    return notch.tickRange.contains(sliderValue);
  });
  return found.label;
};

// snap to the closest preset, and then set the slider value to the preset's location
snapToPreset = function() {
  var found;
  found = presets.find(function(notch) {
    return notch.tickRange.contains(sliderValue);
  });
  sliderValue = found.tickPosition;
  horizonLabel = found.rangeLabel;
  return moveThumbIcon(windowWidth);
};

changePreset = function() {
  snapToPreset();
  return dispatch("update", translatedValue(sliderValue));
};

jumpToPreset = function(preset) {
  var found;
  //console.error "jumpToPreset"
  //console.log preset
  if (preset === "all") { // legacy fix
    preset = "&infin;";
  }
  found = presets.find({
    label: preset
  });
  //console.log "found"
  //console.log found
  sliderValue = found.tickPosition;
  //console.log "sliderValue: #{sliderValue}"
  setIconClass();
  return changePreset();
};

// this repaints the timeline when the parent element has a scrollbar appear or disappear
parentResize = new ResizeObserver(function(e) {
  //console.warn "resizeObserver Fired"
  //console.log e
  return windowResize(windowWidth, windowHeight, el);
});

contentElement = void 0;

onMount(function() {
  setIconClass();
  jumpToPreset(value);
  return leftSplitContentElement.subscribe(function(val) {
    contentElement = val;
    return parentResize.observe(contentElement);
  });
});

onDestroy(function() {
  return parentResize.unobserve(contentElement);
});

windowScrollY = 0;

$: windowResize(windowWidth, windowHeight, el);

$: hasScrollbar = windowWidth > document.body.clientWidth;
</script>
<svelte:window bind:innerWidth={windowWidth} bind:innerHeight={windowHeight}/>
<div horizon bind:this={el} >

  <div>Radar Range: {horizonLabel}</div>
  <div container>
    <div slider>
      <input bind:value={sliderValue} type="range" id="horizon" name="horizon" min="0" max="100" on:change={changePreset} on:input={moveThumbIcon}>
    </div>
    <div slider-track>
    </div>
    <div slider-icon style={iconPosition}>
      <svg width="24" height="24" viewBox="0 0 24 24" version="1.1" class="radar {iconClass}">
          <path class="outer" d="M12,2C6.514,2 2,6.514 2,12C2,17.486 6.514,22 12,22C17.486,22 22,17.486 22,12C22,6.514 17.486,2 12,2M12,4C16.389,4 20,7.611 20,12C20,16.389 16.389,20 12,20C7.611,20 4,16.389 4,12C4,7.611 7.611,4 12,4"/>
          <path class="inner" d="M12,6C8.708,6 6,8.708 6,12C6,15.292 8.708,18 12,18C15.292,18 18,15.292 18,12C18,8.708 15.292,6 12,6M12,8C14.194,8 16,9.806 16,12C16,14.194 14.194,16 12,16C9.806,16 8,14.194 8,12C8,9.806 9.806,8 12,8M12,10C10.903,10 10,10.903 10,12C10,13.097 10.903,14 12,14C13.097,14 14,13.097 14,12C14,10.903 13.097,10 12,10Z"/>
          <path class="center" d="M12,10C10.903,10 10,10.903 10,12C10,13.097 10.903,14 12,14C13.097,14 14,13.097 14,12C14,10.903 13.097,10 12,10Z"/>
      </svg>    
    </div>
    <div slider-ticks>
      {#each presets as tick}
      <div tick style="left: {containerWidth * (tick.tickPosition / 100) - (tick.tickPosition / 3.846153846) + 12}px"></div>
      {/each}
    </div>
    <div slider-tick-labels>
      {#each presets as tick}
      <div tick-label style="left: {containerWidth * (tick.labelPosition / 100) - (tick.labelPosition / 3.846153846) }px">
        <button on:click={jumpToPreset(tick.label)}>
          {@html tick.label}
        </button> 
      </div>
      {/each}    
    </div>
  </div>
</div>

<style lang="sass">svg.radar path {
  fill: var(--light-gray);
}
svg.radar.center path.center {
  fill: var(--red);
}
svg.radar.inner path.center, svg.radar.inner path.inner {
  fill: var(--red);
}
svg.radar.outer path.center, svg.radar.outer path.inner, svg.radar.outer path.outer {
  fill: var(--red);
}

[horizon] {
  margin: 36px 24px;
}

div[container] {
  width: 100%;
  background: transparent;
  height: 72px;
}
div[container] [slider-track] {
  position: absolute;
  background: var(--lighter-gray);
  height: 2px;
  top: 18px;
  left: 12px;
  right: 13px;
  display: block;
  z-index: 0;
}
div[container] [slider-icon] {
  width: 24px;
  height: 24px;
  border-radius: 12px;
  height: 24px;
  margin-left: 1px;
  position: absolute;
  top: 7px;
  background: white;
  z-index: 1;
}
div[container] [slider-ticks] {
  position: absolute;
  top: 18px;
  height: 14px;
  width: 100%;
  z-index: 0;
  padding: 0px;
}
div[container] [slider-ticks] [tick] {
  position: absolute;
  top: 0px;
  width: 1px;
  background: var(--lighter-gray);
  height: 16px;
}
div[container] [slider-ticks] table:first-child {
  width: 100%;
  margin: 0px;
}
div[container] [slider-ticks] table:first-child tr:first-child td {
  border-left: 1px solid #c8c8c8;
  border-right: 0px;
}
div[container] [slider-ticks] table:first-child tr:first-child td:last-child {
  border-right: 1px solid #c8c8c8;
}
div[container] [slider-ticks] table:first-child tr td {
  text-align: right;
}
div[container] [slider-ticks] table:first-child tr td:first-child {
  text-align: left;
}
div[container] [slider-tick-labels] {
  position: absolute;
  top: 36px;
  height: 24px;
}
div[container] [slider-tick-labels] [tick-label] {
  position: absolute;
  top: 0px;
}
div[container] [slider] {
  top: 0px;
  background: transparent;
  z-index: 2;
}
div[container] [slider] input[type=range] {
  padding: 6px 0px;
  border: 0;
  width: 100%;
  margin: 10px 0;
  background-color: transparent;
  -webkit-appearance: none;
}
div[container] [slider] input[type=range]:focus {
  outline: none;
}
div[container] [slider] input[type=range]::-webkit-slider-runnable-track {
  background: transparent;
  border: 1px solid transparent;
  width: 100%;
  height: 2px;
  cursor: pointer;
}
div[container] [slider] input[type=range]::-webkit-slider-thumb {
  margin-top: -10.4px;
  width: 24px;
  height: 24px;
  background: transparent;
  border: 1px solid transparent;
  border-radius: 12px;
  cursor: pointer;
  -webkit-appearance: none;
}

div {
  text-align: center;
}</style>