<script lang="coffeescript">// todo: implement cancel
var click, clickCount, clickReset, delayedReset, dispatch, double, handleBlur, handleInput, handleKeyup, inputEl, originalValue, single;

import {
  tick,
  createEventDispatcher
} from 'svelte';

dispatch = createEventDispatcher();

// todo: cancel

// Props
export var value = '';

export var style = void 0;

export var inputStyle = void 0;

export var type = 'text';

export var placeholder = '';

export var labelClasses = '';

export var inputClasses = '';

export var doubleClick = true;

export var id = void 0;

export var delay = 250;

export var editing = false;

inputEl = void 0;

export var label = "********";

originalValue = void 0;

// Computed
$: isText = type === 'text';


const toggle = async (_) => {
  editing = !editing;
  originalValue = value;
  if (editing) {
    await tick();
    inputEl.focus();
  }
};

handleInput = function(e) {
  return value = e.target.value;
};

handleKeyup = function(e) {
  dispatch("keyup", value);
  switch (e.keyCode) {
    case 13:
      return inputEl.blur();
    case 27:
      value = originalValue;
      return inputEl.blur();
  }
};

// }
handleBlur = function(_) {
  var unchanged;
  unchanged = value === originalValue;
  toggle();
  if (!unchanged) {
    return dispatch("update", value);
  }
};

// Click tracking
clickCount = 0;

clickReset = function() {
  switch (clickCount) {
    case 1:
      return single();
    case 2:
      return double();
    default:
      return clickCount = 0;
  }
};

// }
delayedReset = clickReset.debounce(delay);

single = function() {
  clickCount = 0;
  if (!doubleClick) {
    toggle();
  }
  return dispatch("clicked", id);
};

double = function() {
  clickCount = 0;
  if (doubleClick) {
    toggle();
  }
  return dispatch("doubleclicked", id);
};

click = function() {
  clickCount++;
  return delayedReset();
};
</script>
<style lang="sass">input, div, div span {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  border: 0px solid transparent;
  padding: 0px;
}

input {
  color: var(--dark-red);
}

div {
  min-height: var(--font-xl);
  border: 1px solid transparent;
}</style>
{#if editing }
  <input
    class={inputClasses}
    style={inputStyle}
    bind:this={inputEl}
    {type}
    {value}
    {placeholder}
    on:click|stopPropagation
    on:input={handleInput}
    on:keyup={handleKeyup}
    on:blur={handleBlur}>
{:else}
  <div
    {id}
    class={labelClasses}
    ellipsis {style}>
    <span on:click={click} ellipsis><span ellipsis>{label}<span spacer/></span></span>
  </div>
{/if}
