<script lang="coffeescript">// todo: padding right
var childWidth, parentWidth;

export var style = void 0;

export var offset = 0;

export var ellipsisActive = false;

parentWidth = void 0;

childWidth = void 0;

$: ellipsisActive = ((childWidth + parseInt(offset)) > parentWidth);
</script>



<div container {style} ellipsis={ellipsisActive} {childWidth} {parentWidth} bind:clientWidth={parentWidth}>
  <span subcontainer bind:offsetWidth={childWidth}>
    <slot/>
  </span>
</div>

<style lang="sass">[container] {
  max-width: calc(100% - 1px);
  width: calc(100% - 1px);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: block;
}

[subcontainer] {
  max-width: calc(100% - 1px);
  width: calc(100% - 1px);
}</style>