<script lang="coffeescript">var currentlyDragging, dispatch, draggable, drop, end, enter, isDragging, isInvalid, isOver, leave, mousedown, mouseup, over, start;

import {
  createEventDispatcher,
  onMount
} from "svelte";

dispatch = createEventDispatcher();

import {
  IsDragging,
  CurrentlyDragging
} from "./stores.js";

import {
  FlexRow,
  FlexRowCell
} from "braintapper-svelte-flex";

import {
  IconSvg
} from "braintapper-svelte-icon";

import {
  debug
} from "../../../Braintapper";

export var item = void 0;


// locally tracked store values
currentlyDragging = void 0;

isDragging = false;

onMount(function() {
  IsDragging.subscribe(function(val) {
    return isDragging = val;
  });
  return CurrentlyDragging.subscribe(function(val) {
    return currentlyDragging = val;
  });
});

draggable = false;

isOver = false;

isInvalid = false;

start = function(e) {
  isInvalid = false;
  //console.log "START"
  // console.log item
  CurrentlyDragging.set(item);
  e.dataTransfer.setData("text/plain", JSON.stringify(item.safe_model()));
  IsDragging.set(true);
  return dispatch("dragging", true);
};

end = function(e) { // stop dragging
  CurrentlyDragging.set(void 0);
  IsDragging.set(false);
  isInvalid = false;
  return dispatch("dragStop", true);
};

enter = function(e) {};

// Enter is not a useful event for determining drop validity
// Placeholder for future functionality
over = function(e) {
  // for invalid state, over is much more reliable than enter
  e.preventDefault();
  isOver = true;
  return isInvalid = item.ancestry.includes(currentlyDragging._id) || (item._id === currentlyDragging._id) || (item._id === currentlyDragging.ancestry.last());
};

leave = function(e) {
  draggable = false;
  isOver = false;
  return isInvalid = false;
};

drop = function(e) {
  var dragItem, dropped;
  e.preventDefault();
  dispatch("dragStop", true);
  // todo: consider dropping the e.dataTransfer
  dragItem = JSON.parse(e.dataTransfer.getData("text/plain"));
  //console.log "postparse"
  dropped = true;
  console.log(currentlyDragging); // still exists, not wiped untile END
  if (dropped) {
    if (item.ancestry.includes(currentlyDragging._id) || (item._id === currentlyDragging._id) || (item._id === currentlyDragging.ancestry.last())) {
      console.error("CAN'T DROP ON ANCESTOR OR SELF");
      // todo: toast this
      console.log(`item.ancestry.includes(currentlyDragging.id) ${item.ancestry.includes(currentlyDragging.id)}`);
      console.log(`item.id == currentlyDragging.id ${item.id === currentlyDragging.id}`);
      console.log(`item.id == currentlyDragging.ancestry.last() ${item.id === currentlyDragging.ancestry.last()}`);
      console.log("dragItem");
      console.log(dragItem);
      console.log("currentlyDargign");
      console.log(currentlyDragging);
      console.log("drop item");
      console.log(item);
    } else {
      // console.info "good to go" 
      dispatch("nest", {
        dragItem: dragItem,
        dropItem: item
      });
    }
  }
  isOver = false;
  isInvalid = false;
  return draggable = false;
};

mouseup = function() {
  // console.error "MOUSEUP"
  draggable = false;
  IsDragging.set(false);
  return isInvalid = false;
};

mousedown = function() {
  // IsDragging.set true
  return draggable = true;
};
</script>


<div
  data-id={item._id}
  data-index={item.index}
  data-name={item.name}
  data-ancestry={JSON.stringify(item.ancestry)}
  data-due-date={item.due_date}
  item-draggable
  class:over={isOver}
  class:invalid={isInvalid}
  {draggable}
  on:drop={drop}
  on:dragenter={enter}
  on:dragover={over}
  on:dragstart={start}
  on:dragend={end}
  on:dragleave={leave}

>
  <FlexRow>
    <FlexRowCell flex="initial" >
      <div on:mouseup={mouseup} on:mousedown={mousedown} item-draggable-handle is-dragging={isDragging} visible-on-hover>
        <IconSvg classes="handle" icon="handle"/>
      </div>      
    </FlexRowCell>
    <FlexRowCell flex="true">
      <slot/>
    </FlexRowCell>
    <FlexRowCell flex="initial">
      <div item-draggable-menu  visible-on-hover>
        <slot name="menu"/>
      </div>
    </FlexRowCell>
  </FlexRow>



</div>


<style global lang="sass">:global([item-draggable]) {
  --item-draggable-icon-width: 24px;
  padding: 4px 0px;
  border-radius: 3px;
  width: calc(100% + calc(3 * var(--item-draggable-icon-width)));
  max-width: calc(100% + calc(3 * var(--item-draggable-icon-width)));
  margin-left: calc(var(--item-draggable-icon-width) * -2);
}
:global([item-draggable]) :global([item-draggable-handle]), :global([item-draggable]) :global([item-draggable-menu]) {
  width: var(--item-draggable-icon-width);
}
:global([item-draggable]) :global([item-draggable-handle]) {
  width: 24px;
  height: 24px;
}
:global([item-draggable]) :global([item-draggable-handle]:hover) {
  cursor: grab;
}
:global([item-draggable]) :global([item-draggable-handle][is-dragging=true]) {
  cursor: grabbing !important;
}
:global([item-draggable]) :global([item-draggable-handle]) :global(svg) :global(path), :global([item-draggable]) :global([item-draggable-menu]) :global(svg) :global(path) {
  fill: transparent;
}
:global([item-draggable]) :global([visible-on-hover]) > :global(svg) :global(path) {
  fill: transparent;
}
:global([item-draggable]:hover) {
  background: var(--list-row-highlight);
}
:global([item-draggable]:hover) :global([visible-on-hover]) :global(svg) :global(path) {
  fill: var(--light-gray);
}
:global([item-draggable].over) {
  background: var(--light-blue) !important;
}
:global([item-draggable].invalid), :global([item-draggable].invalid.over) {
  background: var(--light-gray) !important;
}</style>
