<script lang="coffeescript">var component, dispatch, isShowing, setProgress, tippyComponent, tippyObject;

import {
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

import Progress from "./_Progress.svelte";

export var style = void 0;

export var item = void 0;

export var value = {
  percent_complete: 0,
  color: "light-gray"
};

export var size = 24;

tippyObject = void 0;

component = void 0;

isShowing = false;

import {
  IconProgressCircle
} from "braintapper-svelte-icon";

import {
  TippyPopover
} from "../../../Braintapper";

tippyComponent = void 0;

tippyObject = void 0;

setProgress = function(e) {
  var eventObject;
  console.log("setProgress");
  console.debug(e.detail);
  tippyComponent.$set({
    progressValue: e.detail
  });
  eventObject = {
    _id: item._id,
    progress: e.detail
  };
  item.progress = e.detail;
  dispatch("update", eventObject);
  return tippyObject.hide();
};
</script>


<TippyPopover template={Progress} on:setProgress={setProgress} bind:tippyComponent={tippyComponent} bind:tippyObject={tippyObject} theme="progress">
  <div is-showing={isShowing} {style}>
    {#if item.progress }
    <IconProgressCircle value={item.progress.percent_complete} color={item.progress.color} {size}/>
    {/if}
  </div>
</TippyPopover>

<style global lang="sass">:global(.tippy-box[data-theme=progress]) {
  background-color: var(--white);
  border: 1px solid var(--gray);
  border-radius: 0;
  width: 180px;
}</style>
