<script lang="coffeescript">var blur, change, dispatch, input, keyup, textarea;

import {
  onMount,
  createEventDispatcher
} from 'svelte';

export var value = void 0;

export var style = void 0;

export var classes = void 0;

export var id = void 0;

export var placeholder = void 0;

dispatch = createEventDispatcher();

textarea = void 0;

change = function(e) {
  textarea.style.height = ""; // /* Reset the height*/
  return textarea.style.height = (textarea.scrollHeight + 40) + "px"; 
};

keyup = function(e) {
  dispatch("keyup", value);
  change();
  if (e.keyCode === 27) {
    return textarea.blur();
  }
};

input = function(e) {
  return dispatch("input", value);
};

blur = function(e) {
  return dispatch("blur", value);
};

onMount(change);
</script>

<style lang="sass">textarea {
  height: auto;
}</style>


<textarea bind:this={textarea} bind:value on:change={change} on:keyup={keyup} {style} class={classes} {id}
  {placeholder}

    on:input={input}

    on:blur={blur}

></textarea>
