<script lang="coffeescript">var destroyDispatcher, dispatch, goToList, toggleExpanded, updateListItem;

import {
  onMount,
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

import Indent from "braintapper-svelte-indent";

import {
  FlexRow,
  FlexRowCell
} from "braintapper-svelte-flex";

import {
  ButtonIcon
} from "braintapper-svelte-button";

import {
  IconSvg,
  IconToggleSvg
} from "braintapper-svelte-icon";

import {
  debug,
  FormatEllipsis,
  InlineInput,
  SearchHighlighter
} from "../../Braintapper";

import {
  ProgressPopover,
  DueDatePopover,
  ListItemMenu,
  ListItemRow,
  ListHoverableContainer
} from "../../Application";

export var keywords = "";

export var list = void 0;

export var listId = void 0;

goToList = function() {
  return location.href = `#/lists/${listId}`;
};

toggleExpanded = function(item) {
  console.log("toggle Expanded");
  item.is_expanded.radar = !item.is_expanded.radar;
  return dispatch("update", {
    _id: item._id,
    is_expanded: {
      radar: item.is_expanded.radar,
      list: item.is_expanded.list
    }
  });
};

updateListItem = function(e) {
  console.log("Radar._ListItems menu");
  console.log(e.detail);
  if (listId != null) {
    console.log(`top event listid: ${listId}`);
    e.detail["list_id"] = listId;
  }
  return dispatch("update", e.detail);
};

destroyDispatcher = function(e) {
  var showDeleteConfirmation;
  dispatch("destroy", e.detail);
  return showDeleteConfirmation = false;
};
</script>


{#each list as listItem, index}
  
  <ListHoverableContainer>
    <div type="search-item">
      <ListItemRow item={listItem} {keywords} context="search"  classes="search-item" on:update={updateListItem}/>

    </div>
    <div slot="menu">
      <ListItemMenu item={listItem} on:update={updateListItem} on:destroy={destroyDispatcher} context="radar"/>
    </div>
  </ListHoverableContainer>

  
{/each}


