<script>
  import ModalHeader from "./Header.svelte"
  import ModalBody from "./Body.svelte"
  import ModalFooter from "./Footer.svelte"

  import { onDestroy } from 'svelte';

  //window.scrollTo(0,0)
  document.body.style = "overflow:hidden"

  onDestroy( () => {
    document.body.style = ""
  })
  let modal;

  var handle_keydown, previously_focused;

  handle_keydown = function(e) {
    var index, nodes, ref, tabbable;
    if (e.key === 'Escape') {
      close();
      return;
    }

  };


  previously_focused = typeof document !== 'undefined' && document.activeElement;

  if (previously_focused) {
    onDestroy(function() {
      return previously_focused.focus();
    });
  }


</script>



<svelte:window on:keydown={handle_keydown}/>
<modal-background></modal-background>
<modal role="dialog" aria-modal="true" bind:this={modal}>
    <ModalHeader>
        <slot name="header"/>
    </ModalHeader>
    <ModalBody>
        <slot/>
    </ModalBody>
    <ModalFooter>
        <slot name="footer"/>
    </ModalFooter>
</modal>



<style>
  modal-background {
      position: fixed;
      display: block;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.3);
      z-index: 1000;
  }

  modal {
      display: block;
      position: fixed;
      left: 50%;
      top: 50%;
      width: calc(100vw - 4em);
      max-width: 32em;
      max-height: calc(100vh - 4em);
      overflow: show;
      transform: translate(-50%, -50%);
      padding: 24px;
      border-radius: 8px;
      background: white;
      z-index: 1001;
  }




</style>
