<script lang="coffeescript">var addBelow, addDispatcher, cloneListItem, cloneListItemTo, destroyDispatcher, dispatch, dividerDropDispatcher, findSiblingIndex, firstRoot, getAncestry, goToItem, importClipboard, lastCollapsedFromItem, lastRoot, maxIndexForAncestry, moveListItemTo, nestDispatcher, nestNewInto, range, style, updateListItem;

import {
  onMount,
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

import Indent from "braintapper-svelte-indent";

import {
  FlexRow,
  FlexRowCell
} from "braintapper-svelte-flex";

import {
  ButtonIcon
} from "braintapper-svelte-button";

import {
  IconSvg,
  IconToggleSvg
} from "braintapper-svelte-icon";

import {
  debug,
  FormatEllipsis,
  InlineInput
} from "../../Braintapper";

import {
  ListItem,
  ListItemRow,
  ListItemMenu,
  ListDraggableContainer,
  ListDroppableDivider,
  ProgressPopover,
  DueDatePopover
} from "../../Application";

export var list = {};

style = ":hover {font-weight: bold}";

goToItem = function(childItem) {
  return location.href = `#/lists/${childItem._id}`;
};

////                  _       _       _     _     _   ___ _                 
////  _   _ _ __   __| | __ _| |_ ___| |   (_)___| |_|_ _| |_ ___ _ __ ___  
//// | | | | '_ \ / _` |/ _` | __/ _ \ |   | / __| __|| || __/ _ \ '_ ` _ \ 
//// | |_| | |_) | (_| | (_| | ||  __/ |___| \__ \ |_ | || ||  __/ | | | | |
////  \__,_| .__/ \__,_|\__,_|\__\___|_____|_|___/\__|___|\__\___|_| |_| |_|
////       |_|                                                              
updateListItem = function(e) {
  //console.error "#{Date.create().format( "{HH}:{mm}:{ss}.{SSS}")} _ListItems.svelte Update List Item"
  //console.log e.detail

  //console.error "#{Date.create().format( "{HH}:{mm}:{ss}.{SSS}")} _ListItems.svelte Repaint fired"
  list.list_items = list.list_items; // repaint items as required
  // console.error "#{Date.create().format( "{HH}:{mm}:{ss}.{SSS}")} _ListItems.svelte Repaint completed"
  return dispatch("update", e.detail);
};

cloneListItem = function(e) {
  return dispatch("clone", e.detail);
};

cloneListItemTo = function(e) {
  return dispatch("cloneTo", e.detail);
};

////      _           _                   ____  _                 _       _               
////   __| | ___  ___| |_ _ __ ___  _   _|  _ \(_)___ _ __   __ _| |_ ___| |__   ___ _ __ 
////  / _` |/ _ \/ __| __| '__/ _ \| | | | | | | / __| '_ \ / _` | __/ __| '_ \ / _ \ '__|
//// | (_| |  __/\__ \ |_| | | (_) | |_| | |_| | \__ \ |_) | (_| | || (__| | | |  __/ |   
////  \__,_|\___||___/\__|_|  \___/ \__, |____/|_|___/ .__/ \__,_|\__\___|_| |_|\___|_|   
////                                |___/            |_|                                  
destroyDispatcher = function(e) {
  var deletionGroup, deletionIds, showDeleteConfirmation;
  deletionGroup = list.list_items.filter(function(o) {
    return (o._id === e.detail._id) || (o.ancestry.includes(e.detail._id));
  });
  //console.error "destroyDispatcher"
  //console.log deletionGroup

  // UI Repaint
  deletionIds = deletionGroup.map("_id");
  list.list_items.remove(function(o) {
    return deletionIds.includes(o._id);
  });
  list.list_items = list.list_items;
  dispatch("destroy", e.detail);
  return showDeleteConfirmation = false;
};

// drag and drop

////      _ _       _     _           ____                  ____  _                 _       _               
////   __| (_)_   _(_) __| | ___ _ __|  _ \ _ __ ___  _ __ |  _ \(_)___ _ __   __ _| |_ ___| |__   ___ _ __ 
////  / _` | \ \ / / |/ _` |/ _ \ '__| | | | '__/ _ \| '_ \| | | | / __| '_ \ / _` | __/ __| '_ \ / _ \ '__|
//// | (_| | |\ V /| | (_| |  __/ |  | |_| | | | (_) | |_) | |_| | \__ \ |_) | (_| | || (__| | | |  __/ |   
////  \__,_|_| \_/ |_|\__,_|\___|_|  |____/|_|  \___/| .__/|____/|_|___/ .__/ \__,_|\__\___|_| |_|\___|_|   
////                                                 |_|               |_|                                  
dividerDropDispatcher = function(e) {
  console.log(e.detail);
  
  return dispatch("drop", e.detail);
};

/*
navigator.clipboard.readText().then (o)->
  add o
*/
////                  _   ___       _        
////  _ __   ___  ___| |_|_ _|_ __ | |_ ___  
//// | '_ \ / _ \/ __| __|| || '_ \| __/ _ \ 
//// | | | |  __/\__ \ |_ | || | | | || (_) |
//// |_| |_|\___||___/\__|___|_| |_|\__\___/ 
////                                         
nestNewInto = function(e) {
  var newIndex, newStub;
  if (e.detail.list_items.length === 0) {
    newIndex = 0;
  } else {
    newIndex = e.detail.list_items.last().index + 1000;
  }
  newStub = {
    index: newIndex,
    ancestry: e.detail.ancestry.clone().append(e.detail._id),
    name: `New item created at ${Date.create().format('{HH}:{mm}:{ss}')}`
  };
  return addDispatcher({
    detail: newStub
  });
};

////                  _   ____  _                 _       _               
////  _ __   ___  ___| |_|  _ \(_)___ _ __   __ _| |_ ___| |__   ___ _ __ 
//// | '_ \ / _ \/ __| __| | | | / __| '_ \ / _` | __/ __| '_ \ / _ \ '__|
//// | | | |  __/\__ \ |_| |_| | \__ \ |_) | (_| | || (__| | | |  __/ |   
//// |_| |_|\___||___/\__|____/|_|___/ .__/ \__,_|\__\___|_| |_|\___|_|   
////                                 |_|                                  
nestDispatcher = function(e) {
  var changes, dbChanges;
  changes = list.nest_item(e.detail.dragItem, e.detail.dropItem);
  //console.error "nesthandler"
  //console.log changes

  // cycle through cahnges
  changes.forEach(function(change) {
    var found;
    found = list.list_items.find({
      _id: change._id
    });
    if (found != null) {
      found.ancestry = change.ancestry;
      return found.index = change.index;
    }
  });
  list.sort();
  list.list_items = list.list_items; // repaint items as required
  console.error("REPAINT");
  dbChanges = changes.map(function(o) {
    return {
      _id: o._id,
      ancestry: o.ancestry,
      index: o.index
    };
  });
  return dispatch("nest", dbChanges);
};

////            _     _ ____       _               
////   __ _  __| | __| | __ )  ___| | _____      __
////  / _` |/ _` |/ _` |  _ \ / _ \ |/ _ \ \ /\ / /
//// | (_| | (_| | (_| | |_) |  __/ | (_) \ V  V / 
////  \__,_|\__,_|\__,_|____/ \___|_|\___/ \_/\_/  
////                                               
addBelow = function(e) {
  var itemIndex, newIndex, newStub;
  itemIndex = list.list_items.findIndex(function(item) {
    return item._id === e.detail._id;
  });
  if (itemIndex === (list.list_items.length - 1)) {
    newIndex = list.list_items[itemIndex].index + 1000;
  } else {
    newIndex = [list.list_items[itemIndex].index, list.list_items[itemIndex + 1].index].median();
  }
  newStub = {
    index: newIndex,
    ancestry: e.detail.ancestry,
    name: `New item created at ${Date.create().format('{HH}:{mm}:{ss}')}`
  };
  return addDispatcher({
    detail: newStub
  });
};

////       _                  _     _     _   ___ _                 
////   ___| | ___  _ __   ___| |   (_)___| |_|_ _| |_ ___ _ __ ___  
////  / __| |/ _ \| '_ \ / _ \ |   | / __| __|| || __/ _ \ '_ ` _ \ 
//// | (__| | (_) | | | |  __/ |___| \__ \ |_ | || ||  __/ | | | | |
////  \___|_|\___/|_| |_|\___|_____|_|___/\__|___|\__\___|_| |_| |_|
////                                                                
cloneListItem = function(e) {
  return dispatch("clone", e.detail);
};

////       _                  _     _     _   ___ _               _____     
////   ___| | ___  _ __   ___| |   (_)___| |_|_ _| |_ ___ _ __ __|_   _|__  
////  / __| |/ _ \| '_ \ / _ \ |   | / __| __|| || __/ _ \ '_ ` _ \| |/ _ \ 
//// | (__| | (_) | | | |  __/ |___| \__ \ |_ | || ||  __/ | | | | | | (_) |
////  \___|_|\___/|_| |_|\___|_____|_|___/\__|___|\__\___|_| |_| |_|_|\___/ 
////                                                                        
cloneListItemTo = function(e) {
  return dispatch("cloneTo", e.detail);
};

////  _                            _    ____ _ _       _                         _ 
//// (_)_ __ ___  _ __   ___  _ __| |_ / ___| (_)_ __ | |__   ___   __ _ _ __ __| |
//// | | '_ ` _ \| '_ \ / _ \| '__| __| |   | | | '_ \| '_ \ / _ \ / _` | '__/ _` |
//// | | | | | | | |_) | (_) | |  | |_| |___| | | |_) | |_) | (_) | (_| | | | (_| |
//// |_|_| |_| |_| .__/ \___/|_|   \__|\____|_|_| .__/|_.__/ \___/ \__,_|_|  \__,_|
////             |_|                            |_|                                
importClipboard = function(e) {
  return dispatch("importClipboard", e.detail);
};

////                           _     _     _   ___ _               _____     
////  _ __ ___   _____   _____| |   (_)___| |_|_ _| |_ ___ _ __ __|_   _|__  
//// | '_ ` _ \ / _ \ \ / / _ \ |   | / __| __|| || __/ _ \ '_ ` _ \| |/ _ \ 
//// | | | | | | (_) \ V /  __/ |___| \__ \ |_ | || ||  __/ | | | | | | (_) |
//// |_| |_| |_|\___/ \_/ \___|_____|_|___/\__|___|\__\___|_| |_| |_|_|\___/ 
////                                                                         
moveListItemTo = function(e) {
  return dispatch("moveTo", e.detail);
};

addDispatcher = function(e) {
  console.log("addDispatcher");
  return dispatch("add", e.detail);
};

getAncestry = function(list) {
  if (list.doctype === "List") {
    return [];
  } else {
    return (list.ancestry || []).clone().append(list._id);
  }
};


// get the last root item in the list
lastRoot = function(x) {
  var found;
  found = x.list_items.filter(function(o) {
    return o.ancestry.length === 0;
  });
  //console.log "lastRoot"
  if (found.length > 0) {
    
    //console.warn found.last()
    return found.last().index + 1000;
  } else {
    return 1000;
  }
};

firstRoot = function(x) {
  var found;
  found = x.list_items.filter(function(o) {
    return o.ancestry.length === 0;
  });
  //console.log "firstRoot"
  if (found.length > 0) {
    
    //console.warn found.first()
    return found.first().index - 1000;
  } else {
    return 0;
  }
};

range = function(start, finish) {
  if (finish > start) { // next item is deeper than current item
    // do not show any
    return [];
  } else {
    return (function() {
      var results = [];
      for (var i = start; start <= finish ? i <= finish : i >= finish; start <= finish ? i++ : i--){ results.push(i); }
      return results;
    }).apply(this);
  }
};

findSiblingIndex = function(item) {
  var lastSibling, nextSibling, siblingsAfter, siblingsBefore;
  siblingsBefore = list.list_items.filter(function(o) {
    return (o.index < item.index) && o.ancestry.isEqual(item.ancestry);
  });
  siblingsAfter = list.list_items.filter(function(o) {
    return (o.index > item.index) && o.ancestry.isEqual(item.ancestry);
  });
  if (siblingsBefore.length > 0) {
    lastSibling = siblingsBefore.max("index");
    return [lastSibling.index, item.index].median(); // no siblings
  } else {
    if (siblingsAfter.length > 0) {
      nextSibling = siblingsBefore.min("index");
      return nextSibling.index - 1000; // any index
    } else {
      return 1000;
    }
  }
};

lastCollapsedFromItem = function(item) {
  var collapsedDescendants, last;
  if (item.is_collapsed) { // impossible for ancestry to be []
    collapsedDescendants = list.list_items.filter(function(o) {
      return (o.is_expanded.list === false) && item.ancestry.includes(o._id);
    });
    
    //console.error "lastCollapsedFromItem"
    //console.debug collapsedDescendants
    last = collapsedDescendants.max("index");
    return last;
  }
};

maxIndexForAncestry = function(ancestry) {
  var siblings;
  siblings = list.list_items.filter(function(o) {
    return o.ancestry.isEqual(ancestry);
  });
  return siblings.max("index");
};

$: firstIndex = firstRoot(list);

$: lastIndex = lastRoot(list);

$: levelAncestry = getAncestry(list);
</script>


{#if list.list_items}
  <!-- first root level position in the list -->
  {#if list.list_items.length > 0}
  <ListDroppableDivider item={{ _id: list.list_items[0]._id , ancestry: [], index: firstIndex, nest: false} } on:drop={dividerDropDispatcher} on:nest={nestDispatcher} on:add={addDispatcher} index={null} on:importClipboard={importClipboard}/>
  {/if}
  {#each list.list_items as listItem, index}
    {#if !listItem.is_collapsed || (listItem.ancestry.length == 0)}
      <ListDraggableContainer item={listItem} on:nest={nestDispatcher} on:update={updateListItem}>
          <div type="list-item">
            <ListItemRow item={listItem} context="list" on:update={updateListItem}/>
          </div>
          <div slot="menu">
            <ListItemMenu item={listItem} on:update={updateListItem} on:clone={cloneListItem} on:cloneTo={cloneListItemTo} on:moveTo={moveListItemTo} on:importClipboard={importClipboard} on:destroy={destroyDispatcher} on:add={addBelow} on:nest={nestNewInto}/>
          </div>
        
      </ListDraggableContainer>

      <!-- do not show a nesting divider when is_expanded.list == false -->
      {#if listItem.is_expanded.list}
        <ListDroppableDivider item={{  _id: listItem._id ,ancestry: listItem.ancestry.clone().append(listItem._id),  nest: true}} indentation={listItem.ancestry.length + 1} on:drop={dividerDropDispatcher} on:add={addDispatcher} {index} listItems={list.list_items} on:importClipboard={importClipboard}/>
        <div style="height: 4px;"/>
      {/if}



    {/if} <!-- non-collapsed or roots shown -->

    <!-- Last divider(s) for a non parent to take it out the next item ----- (listItem.is_parent == false) &&  -->
    <!-- except when previous item is collapsed? -->
    {#if (index < (list.list_items.length - 1))} <!-- do not include at the bottom of the list -->
      <!-- repeat until next items ancestry level -->
      <!-- do not show if next item is a child -->
      {#if (listItem.is_collapsed == false) } <!-- or last child before next outdent -->
        {#each range(listItem.ancestry.length, list.list_items[index + 1].ancestry.length ) as level, levelIndex}
          <!-- todo: get the index from adjacent siblings -->
            <ListDroppableDivider item={{  _id: listItem._id, ancestry: listItem.ancestry.first(level), index: listItem.index, nest: true, post: true}} indentation={level} on:drop={dividerDropDispatcher} on:nest={nestDispatcher} on:add={addDispatcher} {index} listItems={list.list_items} on:importClipboard={importClipboard}/>
            <div style="height: 4px;"/>

        {/each}

      {/if}

      {#if listItem.is_collapsed && !list.list_items[index + 1].is_collapsed } <!-- or last child before next outdent -->
        
        <ListDroppableDivider item={{ _id: list.list_items[index + 1]._id, ancestry: list.list_items[index + 1].ancestry, index: findSiblingIndex(list.list_items[index + 1]), nest: true, post: true}} indentation={list.list_items[index + 1].ancestry.length} on:drop={dividerDropDispatcher} on:nest={nestDispatcher} on:add={addDispatcher} {index} listItems={list.list_items} on:importClipboard={importClipboard}/>
        <div style="height: 4px;"/>
      {/if}          

    {:else} <!-- last item , there is no "next item", which means ancestry is undefined-->
      {#if listItem.ancestry.length > 0}
          {#if !listItem.is_collapsed}
            {#each range(listItem.ancestry.length, 0 ) as level, levelIndex}
                <ListDroppableDivider item={{ _id: listItem._id, ancestry: listItem.ancestry.first(level), index: (maxIndexForAncestry(listItem.ancestry.first(level)).index + 1000), nest: true, post: true}} indentation={level} on:drop={dividerDropDispatcher} on:nest={nestDispatcher} on:add={addDispatcher} index={null} listItems={list.list_items} on:importClipboard={importClipboard}/>
                <div style="height: 4px;"/>
            {/each}
          {:else}
            {#each range(lastCollapsedFromItem(listItem).ancestry.length, 0 ) as level, levelIndex}
                <ListDroppableDivider item={{ _id: lastCollapsedFromItem(listItem)._id, ancestry: lastCollapsedFromItem(listItem).ancestry.first(level ), index:  (maxIndexForAncestry(lastCollapsedFromItem(listItem).ancestry.first(level )).index + 1000), nest: true, post: true}} indentation={level} on:drop={dividerDropDispatcher} on:nest={nestDispatcher} on:add={addDispatcher} index={null} listItems={list.list_items} on:importClipboard={importClipboard}/>
                <div style="height: 4px;"/>
            {/each}      
          {/if}


      {:else} <!-- root item -->
          <ListDroppableDivider item={{ _id: listItem._id, ancestry: [], index:  (maxIndexForAncestry([]).index + 1000), nest: true, post: true}} indentation={0} on:drop={dividerDropDispatcher} on:nest={nestDispatcher} on:add={addDispatcher} index={null} listItems={list.list_items} on:importClipboard={importClipboard}/>
          <div style="height: 4px;"/>

      {/if}

    {/if} <!-- / last non-parent divider -->



    <!-- todo - find last visible and paint   if required -->

  {/each}
{/if}

<style lang="sass">[type=list-item] {
  width: 100%;
  max-width: 100%;
}
[type=list-item] [main] {
  width: 100px;
}
[type=list-item]:hover [present] {
  display: flex;
}</style>
