<script lang="coffeescript">var debouncedHide, hide;

import {
  fade,
  fly
} from 'svelte/transition';

import {
  afterUpdate
} from "svelte";

export var show = false;

export var message = "Flies in flies out";

export var delay = 6;

hide = function() {
  return show = false;
};

//console.log "HIDE: #{show}"
debouncedHide = hide.debounce(delay.sToMilliseconds());

afterUpdate(function() {
  //console.log "update #{show}"
  if (show) {
    return debouncedHide();
  }
});
</script>

<style lang="sass">div {
  background: var(--darkest-gray);
  color: var(--white);
  padding: 8px 16px;
  position: fixed;
  bottom: 16px;
  margin: 0 auto;
  border-radius: 6px;
  max-width: 300px;
}</style>

{#if show}
	<div in:fly="{{ y: 200, duration: 1000 }}" out:fly="{{ y: 200, duration: 1000 }}" {message}>
		<slot>{@html message}</slot>
	</div>
{/if}
