<script lang="coffeescript">var highlight;

export var keywords = [];

export var text = "";

highlight = function(txt, tokens) {
  var output;
  output = `${txt}`;
  tokens.forEach(function(token) {
    return output = output.replace(new RegExp(`(${token})`, "gi"), "<span class=\"search-highlight\">$1</span>");
  });
  return output;
};

$: highlightedText = highlight(text,keywords);
</script>
{@html highlightedText}
<style global lang="sass">:global(span.search-highlight) {
  background: var(--lighter-yellow);
  font-weight: inherit;
}</style>
