getFileFromFiles = function() {
  var fileArray;
  if (files != null) {
    fileArray = Array.from(files);
    console.debug(fileArray.first());
    return fileArray.first().name;
  } else {
    return "No file selected";
  }
};

click = function(e) {
  console.log("CLICK");
  return e.stopPropagation;
};

$: selectedFile = getFileFromFiles(files);
</script>


