<script lang="coffeescript">var dispatch, goToList, toggleExpanded, updateDate, updateItem;

import {
  createEventDispatcher
} from "svelte";

dispatch = createEventDispatcher();

import Indent from "braintapper-svelte-indent";

import {
  FlexRow,
  FlexRowCell
} from "braintapper-svelte-flex";

import {
  ButtonIcon
} from "braintapper-svelte-button";

import {
  IconSvg,
  IconToggleSvg
} from "braintapper-svelte-icon";

import {
  debug,
  FormatEllipsis,
  InlineInput
} from "../../../Braintapper";

import {
  ProgressPopover,
  DueDatePopover
} from "../../../Application";

export var item = void 0;

export var context = "current";

export var classes = void 0;

toggleExpanded = function() {
  item.is_expanded[context] = !item.is_expanded[context];
  return dispatch("update", {
    _id: item._id,
    is_expanded: item.is_expanded
  });
};

updateDate = function(e) {
  var val;
  val = Date.create(e.detail).format("{yyyy}-{MM}-{dd}");
  return updateItem({
    detail: val
  });
};

updateItem = function(e) {
  return dispatch("update", e.detail);
};

goToList = function() {
  return location.href = `#/lists/${item._id}`;
};
</script>

<FlexRow classes>
  <FlexRowCell flex="initial">
    <div progress>
      <ProgressPopover {item} value={item.progress} on:update={updateItem} on:open on:close/>
    </div>
    
  </FlexRowCell>

  <FlexRowCell flex>
    <InlineInput item={item} on:update={updateItem} on:clicked={goToList}>
      <FormatEllipsis>
        <a href="#/lists/{item._id}" on:click|preventDefault><em>{item.name || "No Names"}</em></a>
      </FormatEllipsis>      
    </InlineInput>
  </FlexRowCell>

  <FlexRowCell flex="initial">
    <div layout="row">
      <div flex="initial" present={item.due_date != null} >
        <DueDatePopover {item} value={item.due_date} on:update={updateDate} on:open on:close/>
      </div>

    </div>
  </FlexRowCell>

</FlexRow>

<style lang="sass">[progress] {
  padding-right: 8px;
}

[collapsible] {
  line-height: 24px;
}
[collapsible] svg, [collapsible] svg.menu {
  display: inline;
}</style>