  margin-bottom: var(--spacing-m);
}
header h1 {
  font-size: 36px;
  color: var(--darkest-blue);
}

[horizon-label] {
  margin-top: 18px;
  margin-bottom: 36px;
  width: 100%;
}
[horizon-label] p {
  margin: 0px 24px;
  color: var(--darker-gray);
  text-align: center;
}

[collapsible], [menu], [progress] {
  width: 32px;
}

.radar-list {
  max-height: 42px;
  border-radius: 3px;
  width: calc(100% + 96px);
  max-width: calc(100% + 96px);
  margin-left: -64px;
  margin-bottom: 8px;
  box-sizing: border-box;
  padding: 4px 0px;
}
.radar-list [menu] {
  text-align: center;
}
.radar-list .slot {
  width: 100%;
  max-width: 100%;
}
