fixIdMismatch = function() {
  var selector;
  console.error("fixIdMismatch");
  selector = {
    $or: [
      {
        doctype: "List"
      },
      {
        doctype: "ListItem"
      }
    ]
  };
  return db.find({
    selector: selector
  }).then(function(result) {
    var mismatched;
    mismatched = result.docs.filter(function(o) {
      return o.id !== o._id;
    });
    console.error("MISMATCHED");
    console.log(mismatched);
    mismatched = mismatched.map(function(o) {
      o.id = o._id;
      return o;
    });
    console.log("READY TO FIX");
    return db.bulkDocs(mismatched).then(function(response) {
      console.error("bulk update mismatches");
      return console.debug(response);
    });
  });
};

fixOrphans = function() {
  var selector;
  selector = {
    $or: [
      {
        doctype: "List"
      },
      {
        doctype: "ListItem"
      }
    ]
  };
  return db.find({
    selector: selector
  }).then(function(result) {
    var bulkDocs, foundDocs, listItemProjectIds, newOrphanId, newOrphanList, orphans, projectIds;
    console.log("result docs");
    console.debug(result.docs);
    foundDocs = result.docs.groupBy("doctype");
    console.error("foundDocs");
    console.log(foundDocs);
    foundDocs["List"] = foundDocs["List"].groupBy("id");
    foundDocs["ListItem"] = foundDocs["ListItem"].groupBy("list_id");
    projectIds = Object.keys(foundDocs["List"]);
    listItemProjectIds = Object.keys(foundDocs["ListItem"]);
    console.error("projectIds");
    console.debug(projectIds);
    console.error("listItemProjectIds");
    console.debug(listItemProjectIds);
    orphans = [];
    listItemProjectIds.forEach(function(listId) {
      if (foundDocs["List"][listId] != null) {
        return console.log(`${listId} found, should be valid, skpping`);
      } else {
        console.log(`${listId} is orphaned`);
        return orphans.append(listId);
      }
    });
    console.error("ORPHAN list ids");
    console.debug(orphans);
    newOrphanList = new List;
    newOrphanList.name = "New Orphan List";
    newOrphanList = newOrphanList.pouchify();
    newOrphanId = newOrphanList_id;
    bulkDocs = [];
    orphans.forEach(function(orphanListId) {
      console.log(`orphanLIstId ${orphanListId}`);
      console.debug(foundDocs["ListItem"]);
      console.debug(foundDocs["ListItem"][orphanListId]);
      return bulkDocs.append(foundDocs["ListItem"][orphanListId].map(function(o) {
        o.list_id = newOrphanId;
        return o;
      }));
    });
    console.error("bulkDocs");
    console.log(bulkDocs);
    console.error("NEW ORPHAN LIST");
    console.log(newOrphanList);
    return db.put(newOrphanList).then(function(response) {
      return db.bulkDocs(bulkDocs).then(function(response) {
        console.error("bulk update orphans");
        return console.debug(response);
      });
    }).catch(function(err) {
      if (typeof catchCb !== "undefined" && catchCb !== null) {
        return catchCb(err);
      } else {
        console.log("insert.catch");
        return console.log(err);
      }
    }).finally(function() {
      console.log("insert finally");
      if (typeof finallyCb !== "undefined" && finallyCb !== null) {
        console.log("insert.finally");
        return finallyCb();
      }
    });
  });
};


