importData = function() {
  var fileArray, reader;
  console.error("importData");
  console.debug(files);
  if (files != null) {
    console.log("file info");
    fileArray = Array.from(files);
    console.debug(fileArray.first());
    reader = new FileReader();
    reader.onloadend = function(response) {
      var bulkData, importedData, jsonToData;
      console.log("READ FILE");
      console.log(response);
      jsonFile = response.target.result;
      jsonToData = JSON.parse(jsonFile).map(function(o) {
        delete o._rev; // clean
        return o;
      });
      importedData = jsonToData.groupBy("doctype");
      if (Object.has(importedData, "List")) {
        importedData["List"] = importedData["List"].groupBy("id");
      } else {
        importedData["List"] = void 0;
      }
      if (Object.has(importedData, "ListItem")) {
        importedData["ListItem"] = importedData["ListItem"].groupBy("list_id");
      } else {
        importedData["ListItem"] = void 0;
      }
      console.error("imported data by doctype");
      console.debug(importedData);
      if (Object.has(importedData, "List")) {
        bulkData = [];
        return Object.keys(importedData["List"]).forEach(function(key) {
          var newKeys;
          // rekey the project
          importedData["List"][key].first()["id"] = uuid();
          importedData["List"][key].first()["_id"] = importedData["List"][key].first()["id"];
          importedData["List"][key].first()["name"] = `${(importedData["List"][key].first()["name"])} imported at ${Date.create()}`;
          bulkData.append(importedData["List"][key].first());
          console.log(`Rekeyed list id ${key} to ${importedData["List"][key]._id}`);
          // handle list items for list
          if (importedData["ListItem"] != null) {
            if (Object.has(importedData["ListItem"], key)) {
              console.log("found list items");
              console.log(`${importedData["List"][key].name} has ${importedData["ListItem"][key].length} list items`);
              // go throug all items
              newKeys = {};
              importedData["ListItem"][key].sortBy(function(listItem) {
                return listItem.ancestry.length;
              });
              importedData["ListItem"][key].forEach(function(listItem) {
                var oldId;
                listItem.list_id = importedData["List"][key].first().id; // rekey list id to previously created
                oldId = `${listItem.id // add the current item key to key map
}`;
                listItem["id"] = uuid();
                listItem["_id"] = listItem["id"];
                newKeys[oldId] = listItem.id;
                return listItem.ancestry = listItem.ancestry.map(function(ancestor) {
                  if (newKeys[ancestor]) {
                    return newKeys[ancestor];
                  } else {
                    return ancestor;
                  }
                });
              });
              console.log("newKeys");
              console.log(newKeys);
              console.error("finalized importedData");
              console.log(importedData);
            } else {
              console.log(`${(importedData["List"][key].first().name)} has no list items`);
            }
            bulkData.append(importedData["ListItem"][key]);
          }
          console.debug(bulkData);
          return db.bulkDocs(bulkData).then(function(result) {
            console.error("imported");
            return console.debug(result);
          });
        });
      }
    };
    
    //jsonToData.forEach (item)->
    //item.id = uuid()
    //item["_id"] = item.id
    // item.name = "#{item.name} imported at #{Date.create()}"

    //db.bulkDocs(jsonToData)
    //.then (result)->
    //  console.error "imported"
    //  console.debug result
    return reader.readAsText(fileArray.first());
  }
};

notificationPermssionRequest = function() {
  return Notification.requestPermission().then(function(permission) {
    var notification;
    // If the user accepts, let''s create a notification
    if (permission === 'granted') {
      notification = new Notification('Hi there!');
    } else {
      console.log("not granted");
    }
  });
};

if (electron) {
  edition = "Desktop";
} else {
  edition = "Web";
}

// resource
compact = function() {
  return db.compact().then(function(result) {
    console.log("COMPACTED");
    return console.log(result);
  });
};

deleteIndices = function() {
  return db.getIndexes().then(function(indexesResult) {
    return indexesResult.indexes.forEach((index) => {
      if (index.name !== "_all_docs") {
        console.log(index);
        return db.deleteIndex(index);
      }
    });
  });
};

rawDump = function() {
  // full export
  return db.allDocs({
    include_docs: true,
    attachments: false
  }).then(function(result) {
    var blob, intermediate, jsonString;
    intermediate = result.rows.map(function(row) {
      return row.doc;
    });
    jsonString = JSON.stringify(intermediate, null, 2);
    blob = new Blob([jsonString], {
      type: "text/plain;charset=utf-8"
    });
    return FileSaver.saveAs(blob, `braintapper-radar-dump-${Date.create().toTimestampId()}.json`);
  });
};

saveAs = function() {
  // full export
  return db.allDocs({
    include_docs: true,
    attachments: false
  }).then(function(result) {
    var blob, groupedListItems, intermediate, jsonString, listItems, lists, orphanedItems, others, output;
    intermediate = result.rows.map(function(row) {
      return row.doc;
    });
    console.log(`Intermediate items: ${intermediate.length}`);
    output = [];
    orphanedItems = [];
    lists = intermediate.filter(function(item) {
      delete item._rev;
      delete item._id;
      return item.doctype === "List";
    });
    lists.sortBy("name");
    console.log(`LISTS ${lists.length}`);
    console.log(lists);
    listItems = intermediate.filter(function(item) {
      return item.doctype === "ListItem";
    });
    others = intermediate.filter(function(item) {
      var test;
      test = false;
      if (item.doctype != null) {
        if (!["List", "ListItem"].includes(item.doctype)) {
          test = true;
        }
      } else {
        if (item.id != null) {
          test = true;
        }
      }
      return test;
    });
    orphanedItems.append(others);
    console.log(`----------- process list items ${listItems.length}`);
    console.log(`others: ${others.length}`);
    console.log(listItems);
    console.log(others);
    groupedListItems = listItems.groupBy("list_id");
    // console.debug groupedListItems
    lists.forEach(function(list, index) {
      //console.log "LIST"
      //console.log list
      output.append(list);
      if (index < 2) {
        console.debug(output);
      }
      if (groupedListItems[list.id] != null) {
        return output.append(groupedListItems[list.id].sortBy(function(listItem) {
          return listItem.ancestry.join(" ");
        }));
      } else {
        return orphanedItems.append(groupedListItems[list.id]);
      }
    });
    output.append(orphanedItems.map(function(o) {
      o["list_id"] = "orphan";
      return o;
    }));
    jsonString = JSON.stringify(output, null, 2);
    blob = new Blob([jsonString], {
      type: "text/plain;charset=utf-8"
    });
    return FileSaver.saveAs(blob, `braintapper-radar-archive-${Date.create().toTimestampId()}.json`);
  });
};

destroy = function() {
  return db.destroy().then(function(response) {
    console.log("destroyed");
    console.debug(response);
    window.createPouchDb();
    return window.mounted();
  }).catch(function(err) {
    console.error("destroy error");
    return console.log(err);
  });
};

